___CSS_LOADER_EXPORT___.push([module.id, `/* Reset default margin and padding */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: 'Poppins', sans-serif;
    line-height: 1.6;
}

/* Survey Description Styling */
.survey-description {
    max-width: 1200px;
    margin: 0 auto;
    text-align: center;
}

.content-one {
    text-align: center;
    width: 100%;
    margin-top: 5%;
}

#p1 {
    text-align: center;
    font-size: 20px;
    max-width: 70ch;
    margin: 50px auto 20px;
    word-wrap: break-word;
    color: black;
}

#hasil {
    margin-top: 50px;
}

/* Button Styling */
button {
    background-color: #008080;
    border: none;
    color: white;
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 10px 5px;
    margin-top: 20px;
    cursor: pointer;
    border-radius: 12px;
    transition: background-color 0.3s, transform 0.3s;
}

button:hover {
    transform: scale(1.05);
}

button:active {
    background-color: #3e8e41;
}

/* Form Section Styling */
.form-section {
    text-align: center;
    
}

.form-section form {
    display: inline-block;
    text-align: left;
    width: 700px; /* Set a fixed width */
    padding: 20px;
    background: #f9f9f9;
    border-radius: 8px;
    margin: 20px 20px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

#formPerokok form p {
    text-align: left;
}

#formPerokok form button {
    text-align: center;
    margin: 10px auto; /* Center the buttons horizontally */
}

/**
 * benefits-content
 */

 #benefits {
    margin-top: 20px;
    margin-bottom: 50px;
  }
  
  .benefits-content {
    color: black;
    max-width: 1200px;
    margin: 0 auto;
    text-align: center;
  }
  
  .benefits-content .benefits-title {
    font-size: 20px;
    margin-bottom: 30px;
  }

  .benefits-title p {
    margin-top: 20px;
  }
  
  .benefits-content .benefits-container {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
    gap: 2rem;
  }
  
  .benefits-content .card-benefit {
    background: white;
    padding-bottom: 10px;
    padding-left: 10px;
    padding-right: 10px;
    border-radius: 20px;
    text-align: center;
    color: white;
    background-color: #008080;
    transition: all 0.3s ease;
    box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px,
      rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
  }
  
  .benefits-content .card-benefit:hover {
    transform: translateY(-10px);
  }
  
  .benefits-content .user-picture img {
    max-width: 120px;
    height: auto;
    text-align: center;
    margin-top: 10px;
  }`, "",{"version":3,"sources":["webpack://./src/styles/survey-style.css"],"names":[],"mappings":"AAAA,qCAAqC;AACrC;IACI,SAAS;IACT,UAAU;IACV,sBAAsB;AAC1B;;AAEA;IACI,kCAAkC;IAClC,gBAAgB;AACpB;;AAEA,+BAA+B;AAC/B;IACI,iBAAiB;IACjB,cAAc;IACd,kBAAkB;AACtB;;AAEA;IACI,kBAAkB;IAClB,WAAW;IACX,cAAc;AAClB;;AAEA;IACI,kBAAkB;IAClB,eAAe;IACf,eAAe;IACf,sBAAsB;IACtB,qBAAqB;IACrB,YAAY;AAChB;;AAEA;IACI,gBAAgB;AACpB;;AAEA,mBAAmB;AACnB;IACI,yBAAyB;IACzB,YAAY;IACZ,YAAY;IACZ,kBAAkB;IAClB,kBAAkB;IAClB,qBAAqB;IACrB,qBAAqB;IACrB,eAAe;IACf,gBAAgB;IAChB,gBAAgB;IAChB,eAAe;IACf,mBAAmB;IACnB,iDAAiD;AACrD;;AAEA;IACI,sBAAsB;AAC1B;;AAEA;IACI,yBAAyB;AAC7B;;AAEA,yBAAyB;AACzB;IACI,kBAAkB;;AAEtB;;AAEA;IACI,qBAAqB;IACrB,gBAAgB;IAChB,YAAY,EAAE,sBAAsB;IACpC,aAAa;IACb,mBAAmB;IACnB,kBAAkB;IAClB,iBAAiB;IACjB,uCAAuC;AAC3C;;AAEA;IACI,gBAAgB;AACpB;;AAEA;IACI,kBAAkB;IAClB,iBAAiB,EAAE,oCAAoC;AAC3D;;AAEA;;EAEE;;CAED;IACG,gBAAgB;IAChB,mBAAmB;EACrB;;EAEA;IACE,YAAY;IACZ,iBAAiB;IACjB,cAAc;IACd,kBAAkB;EACpB;;EAEA;IACE,eAAe;IACf,mBAAmB;EACrB;;EAEA;IACE,gBAAgB;EAClB;;EAEA;IACE,aAAa;IACb,2DAA2D;IAC3D,SAAS;EACX;;EAEA;IACE,iBAAiB;IACjB,oBAAoB;IACpB,kBAAkB;IAClB,mBAAmB;IACnB,mBAAmB;IACnB,kBAAkB;IAClB,YAAY;IACZ,yBAAyB;IACzB,yBAAyB;IACzB;4CACwC;EAC1C;;EAEA;IACE,4BAA4B;EAC9B;;EAEA;IACE,gBAAgB;IAChB,YAAY;IACZ,kBAAkB;IAClB,gBAAgB;EAClB","sourcesContent":["/* Reset default margin and padding */\r\n* {\r\n    margin: 0;\r\n    padding: 0;\r\n    box-sizing: border-box;\r\n}\r\n\r\nbody {\r\n    font-family: 'Poppins', sans-serif;\r\n    line-height: 1.6;\r\n}\r\n\r\n/* Survey Description Styling */\r\n.survey-description {\r\n    max-width: 1200px;\r\n    margin: 0 auto;\r\n    text-align: center;\r\n}\r\n\r\n.content-one {\r\n    text-align: center;\r\n    width: 100%;\r\n    margin-top: 5%;\r\n}\r\n\r\n#p1 {\r\n    text-align: center;\r\n    font-size: 20px;\r\n    max-width: 70ch;\r\n    margin: 50px auto 20px;\r\n    word-wrap: break-word;\r\n    color: black;\r\n}\r\n\r\n#hasil {\r\n    margin-top: 50px;\r\n}\r\n\r\n/* Button