___CSS_LOADER_EXPORT___.push([module.id, `/* Media Queries for Responsiveness */
@media screen and (max-width: 1024px) {
}

@media screen and (max-width: 768px) {
  body {
    box-sizing: border-box;
  }

  .content {
    width: 100%;
  }

  .content-image img {
    max-width: 350px;
  }

  .welcome-content {
    display: flex;
    flex-direction: column-reverse;
    text-align: center;
    margin-bottom: 50px;
    justify-content: space-evenly;
    min-height: 50vh;
  }

  .welcome-content .content-image {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 300px;
  }

  .cigarette-content .content-box {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  .cigarette-content .content-box img {
    max-width: 300px;
  }

  .smoking-disease {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .smoking-disease .disease-container {
    grid-template-columns: 1fr;
    max-width: 600px;
  }

  .smoking-disease .disease-container .disease-body {
    padding: 0;
  }

  .comments-container .commentbox,
  .comment-list {
    flex-direction: column;
  }
}

@media screen and (max-width: 480px) {
}
