___CSS_LOADER_EXPORT___.push([module.id, `.about-us {
    max-width: 1200px;
    margin: 0 auto;
    padding: 70px;
    text-align: center;
}

.about-us .gambar {
    display: flex;
    align-items: center;
    justify-content: center;
}

.about-us h2 {
    padding: 10px;
}

#content {
    background-color: #008080;
}

.about-website {
    max-width: 1400px;
    margin: 0 auto;
    padding: 40px;
    text-align: center;
}

.about-website h1 {
    color: white;
}

.about-website p {
    text-align: justify;
    color: white;
    line-height: 1.5;
}

.about-dev {
    max-width: 1400px;
    margin: 0 auto;
    padding: 55px;
    text-align: center;
}

.about-dev p {
    text-align: justify;
    line-height: 1.5;
}

.about-dev .dev-container {
    padding: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 2rem;
}

.about-dev .card-dev {
    background: #008080;
    padding: 25px;
    border-radius: 20px;
    text-align: center;
    color: white;
    transition: all 0.3s ease;
    box-shadow: 0 7px 15px 0 rgba(0, 0, 0, 0.295), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.card-dev .dev-title {
    margin-bottom: 20px;
}

.about-dev .card-dev:hover {
    transform: translateY(-10px);
}

.about-dev .dev-picture {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px;
}

.about-dev .dev-picture img {
    width: 150px;
    height: 150px;
    border-radius: 50%;
    justify-content: center;
}

.about-dev .dev-title {
    color: white;
    font-size: 18px;
}

.about-dev .dev-container .icon-picture {
    margin: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 0.8rem;
}

.dev-container .icon-picture img {
    width: 40px;
    height: 40px;
    border-radius: 10px;
}

.dev-container .icon-picture img:hover {
    transform: translateY(-10px);
    transition: all 0.3s ease;
}`, "",{"version":3,"sources":["webpack://./src/styles/about-style.css"],"names":[],"mappings":"AAAA;IACI,iBAAiB;IACjB,cAAc;IACd,aAAa;IACb,kBAAkB;AACtB;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,uBAAuB;AAC3B;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI,yBAAyB;AAC7B;;AAEA;IACI,iBAAiB;IACjB,cAAc;IACd,aAAa;IACb,kBAAkB;AACtB;;AAEA;IACI,YAAY;AAChB;;AAEA;IACI,mBAAmB;IACnB,YAAY;IACZ,gBAAgB;AACpB;;AAEA;IACI,iBAAiB;IACjB,cAAc;IACd,aAAa;IACb,kBAAkB;AACtB;;AAEA;IACI,mBAAmB;IACnB,gBAAgB;AACpB;;AAEA;IACI,aAAa;IACb,aAAa;IACb,mBAAmB;IACnB,uBAAuB;IACvB,SAAS;AACb;;AAEA;IACI,mBAAmB;IACnB,aAAa;IACb,mBAAmB;IACnB,kBAAkB;IAClB,YAAY;IACZ,yBAAyB;IACzB,+EAA+E;AACnF;;AAEA;IACI,mBAAmB;AACvB;;AAEA;IACI,4BAA4B;AAChC;;AAEA;IACI,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,aAAa;AACjB;;AAEA;IACI,YAAY;IACZ,aAAa;IACb,kBAAkB;IAClB,uBAAuB;AAC3B;;AAEA;IACI,YAAY;IACZ,eAAe;AACnB;;AAEA;IACI,YAAY;IACZ,aAAa;IACb,mBAAmB;IACnB,uBAAuB;IACvB,WAAW;AACf;;AAEA;IACI,WAAW;IACX,YAAY;IACZ,mBAAmB;AACvB;;AAEA;IACI,4BAA4B;IAC5B,yBAAyB;AAC7B","sourcesContent":[".about-us {\r\n    max-width: 1200px;\r\n    margin: 0 auto;\r\n    padding: 70px;\r\n    text-align: center;\r\n}\r\n\r\n.about-us .gambar {\r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: center;\r\n}\r\n\r\n.about-us h2 {\r\n    padding: 10px;\r\n}\r\n\r\n#content {\r\n    background-color: #008080;\r\n}\r\n\r\n.about-website {\r\n    max-width: 1400px;\r\n    margin: 0 auto;\r\n    padding: 40px;\r\n    text-align: center;\r\n}\r\n\r\n.about-website h1 {\r\n    color: white;\r\n}\r\n\r\n.about-website p {\r\n    text-align: justify;\r\n    color: white;\r\n    line-height: 1.5;\r\n}\r\n\r\n.about-dev {\r\n    max-width: 1400px;\r\n    margin: 0 auto;\r\n    padding: 55px;\r\n    text-align: center;\r\n}\r\n\r\n.about-dev p {\r\n    text-align: justify;\r\n    line-height: 1.5;\r\n}\r\n\r\n.about-dev .dev-container {\r\n    padding: 20px;\r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: center;\r\n    gap: 2rem;\r\n}\r\n\r\n.about-dev .card-